import { POST_SUCCESS, POST_FAILURE } from './timesheetAction'


const prevState = {
    isDataPosted: false
}

const postDataReducer = (state = prevState, action) => {
    console.log("action type:"+action.type+"############################################")
    console.log("isDataposted:"+action.isDataPosted)
    switch (action.type) {
        
        case '@@redux-form/CHANGE':
            return Object.assign({}, state, {
                isDataPosted: action.isDataPosted
            })
        // case POST_FAILURE:
        //     return Object.assign({}, state, {
        //         dataPosted: action.dataPosted
        //     })
        default:
            return state
    }
}

export default postDataReducer


